import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'
var _self, page = 1,
	timer = null;
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			src: '',
			vid: '0',
			topheight: 0,
			phoneHeight: 0,
			status: 'more',
			page: 1,
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			},
			listsss: [],
			lists: []
		}
	},

	onShareAppMessage(res) {
		var token = uni.getStorageSync('authorization');
		if (token == '' || token == null) {
			uni.showModal({
				cancelText: '取消登录',
				confirmText: '立即登陆',
				title: '提示',
				content: '是否确定授权登陆？',
				success: function(res) {
					if (res.confirm) {
						uni.navigateTo({
							url: '../myinfo/login'
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
			return
		}
		uni.showShareMenu({
			withShareTicket: true
		})
		return {

			title: '' + _self.lists[this.vid].title,
			path: '/pages/videoshare/videoshare?videourl=' + _self.lists[this.vid].url + '&imageUrl=' + _self.lists[this.vid].imageUrl +
				'&strtitle=' + _self.lists[this.vid].title,
			success: res => {
				console.log('--- 转发回调 ---', res);
				//onShareAppMessage回调的shareTickets，如果没有，就说明不是转发到群聊的
				console.log('--- shareTickets ---', res.shareTickets);
				//转发到群里的才会有shareTickets
				if (res.shareTickets && res.shareTickets[0]) {
					//获取转发的详细信息
					uni.getShareInfo({
						shareTicket: res.shareTickets[0],
						success: res => {
							console.log('--- 错误信息 ---', res.errMsg);
							console.log('--- 包括敏感数据在内的完整转发信息的加密数据 ---', res.encryptedData);
							console.log('--- 错误信息 ---', res.iv);
						},
						fail: error => {
							console.log('--- getShareInfo fail ---', error);
						}
					})
				}
			},
			fail: () => {
				console.log('--- 转发失败 ---', path);
			}
		}
	},
	onReady: function(res) {

		this.videoContext = uni.createVideoContext('myVideo')

		var that = this;
		uni.getSystemInfo({
			success(res) {
				that.phoneHeight = res.windowHeight;
				console.log(res.windowHeight);
			}
		});
	},
	onLoad: function() {
		_self = this;
		this.getnewsList();
	},
	onShow: function(res) {

	},
	onPullDownRefresh: function() {

		this.getnewsList();
	},
	onReachBottom: function() {
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_self.getmorenews();
		}, 100);
	},
	onPageScroll: function(Object) {
		console.log(Object.scrollTop); //实时获取到滚动的值
		var gaodu = this.topheight + 220 - this.phoneHeight
		if (gaodu < 0) {
			gaodu = 0;
		}
		console.log(gaodu); //实时获取到滚动的值
		if (Object.scrollTop > this.topheight + 120 || Object.scrollTop < gaodu) {
			this.videoContext = uni.createVideoContext(this.vid + '', this)
			if (this.videoContext) {
				this.videoContext.pause()
			}
		}
	},
	methods: {
		dianjishijian: function(e) {
			var token = uni.getStorageSync('authorization');
			if (token == '' || token == null) {
				uni.showModal({
					cancelText: '取消登录',
					confirmText: '立即登陆',
					title: '提示',
					content: '是否确定授权登陆？',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '../myinfo/login'
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
				return
			}

			if (this.vid != e) {
				this.videoContext = uni.createVideoContext(this.vid + '', this)
				if (this.videoContext) {
					this.videoContext.pause()
				}
				_self.lists[this.vid].countComment = 0;
			}

			this.vid = e + '';
			this.topheight = e * 220;
			console.log('videoid=' + this.vid);

			_self.lists[e].countComment = 1;
			this.videoContext = uni.createVideoContext(this.vid + '', this)
			if (this.videoContext) {

				setTimeout(() => {
					this.videoContext.play();
				}, 100);
			}
		},
		getmorenews: function() {
			_self.status = 'loading';
			console.log('page=' + _self.page);
			const accountInfo = uni.getAccountInfoSync();
			var appId = accountInfo.miniProgram.appId; // 小程序 appId
			uni.showNavigationBarLoading();
			uni.request({
				url: _self.$api_url + '/short_video/list',
				method: 'post',
				data: {
					pageNum: _self.page,
					pageSize: 10,
					appId:appId
				},
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					// 'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				success: function(res) {
					console.log(res.data);
					if (res.data.code != 1000) {
						uni.hideNavigationBarLoading();
						_self.status = 'noMore';
						return false;
					}
					_self.page = _self.page + 1;

					for (var i = 0; i < res.data.content.length; i++) {
						var strbena = {
							countComment: 0,
							id: 99,
							imageUrl: "https://zzwl1zsxn.oss-cn-hangzhou.aliyuncs.com/images/2019/07/04/a21b8e5cddc5866a0ac3ff9af788f6c6.jpg",
							title: "小伙分析造成葡萄黄叶化的原因！水肥至关重要！看他是咋解决的",
							url: "https://zzwl1zsxn.oss-cn-hangzhou.aliyuncs.com/zhxn_video/2019/07/96ad620ee1aa29e2ec144144438ab3a0.mp4",
							userName: '智慧新农'
						}
						strbena.countComment = 0
						strbena.id = res.data.content[i].id
						strbena.imageUrl = res.data.content[i].imageUrl
						strbena.title = res.data.content[i].title
						strbena.url = res.data.content[i].url
						strbena.userName = res.data.content[i].userName
						_self.lists = _self.lists.concat(strbena)
					}

					// _self.lists = _self.lists.concat(res.data.content);
					_self.status = 'more';
					uni.hideNavigationBarLoading();
				}
			});
		},
		getnewsList: function() {
			_self.page = 1;
			_self.status = 'loading';
			const accountInfo = uni.getAccountInfoSync();
			var appId = accountInfo.miniProgram.appId; // 小程序 appId
			uni.showNavigationBarLoading();
			uni.request({
				url: _self.$api_url + '/short_video/list',
				method: 'post',
				data: {
					pageNum: 1,
					pageSize: 10,
					appId:appId
				},
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					// 'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				success: function(res) {
					console.log(res);
					if (res.data.code != 1000) {
						uni.hideNavigationBarLoading();
						_self.status = 'noMore';
						return false;
					}
					_self.page = _self.page + 1;
					_self.listsss = res.data.content;
					console.log(_self.listsss);
					_self.lists = [];
					for (var i = 0; i < _self.listsss.length; i++) {
						var strbena = {
							countComment: 0,
							id: 0,
							imageUrl: "jpg",
							title: "小伙分析造成葡萄黄叶化的原因！水肥至关重要！看他是咋解决的",
							url: "mp4",
							userName: '智慧新农'
						}
						strbena.countComment = 0
						strbena.id = _self.listsss[i].id
						strbena.imageUrl = _self.listsss[i].imageUrl
						strbena.title = _self.listsss[i].title
						strbena.url = _self.listsss[i].url
						strbena.userName = res.data.content[i].userName
						_self.lists = _self.lists.concat(strbena)
					}
					// _self.lists = _self.lists.concat(res.data.content);
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh();
					_self.status = 'more';
					_self.vid = '0';
				}
			});
		}
	}
}
